import React from 'react';
import { IconButton, Button, Box, Container, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

import Logo from '../../assets/images/logo.svg';

const Header = () => {
    const onWalletConnect = () => {

    }
    return(
        <Box
            display='flex'
            justifyContent='space-between'
            alignItems='center'
            fontFamily= 'Audiowide'
            marginY='12px'
            sx={{
                backgroundColor: 'black'
            }}
        >
            <Box component='img' width={{xs: '70px', sm: '100px'}} src={Logo} />
            <Button
                variant="text"
                sx={{
                    display: { xs: 'none', sm: "flex" },
                    color: 'text.primary',
                    fontFamily: 'inherit',
                }}
            >
                ROADMAP
            </Button>
            <Button
                variant='contained'
                sx={{
                    fontFamily: 'inherit',
                }}
                onClick={onWalletConnect}
            >
                CONNECT
            </Button>
            <IconButton
                sx={{display: {xs: 'flex', sm: 'none'}}}
            >
                <MenuIcon 
                    sx={{ color: 'primary.light'}}
                />
            </IconButton>
        </Box>
    );
}

export default Header;