import React from 'react';
import { IconButton, Button, Box, Container, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

import Logo from '../../assets/images/logo.svg';

const Footer = () => {
    const onWalletConnect = () => {

    }
    return(
        <Box
            display='flex'
            justifyContent='space-between'
            alignItems='center'
            fontFamily= 'Audiowide'
            marginY='20px'
            sx={{
                backgroundColor: 'black'
            }}
        >
            <Box 
                component='img' 
                width='100px'
                display={{md: 'flex', xs: 'none'}}
                src={Logo} 
            />
            <Box
                display={{md: 'flex', xs: 'none'}}
            >
                <Button
                    variant="text"
                    sx={{
                        color: 'text.primary',
                        fontFamily: 'inherit',
                        marginRight: '30px',
                        fontSize: '24px'
                    }}
                >
                    DISCORD
                </Button>
                <Button
                    variant="text"
                    sx={{
                        color: 'text.primary',
                        fontFamily: 'inherit',
                        fontSize: '24px'
                    }}
                >
                    TWITTER
                </Button>
            </Box>
            <Button
                variant="text"
                sx={{
                    display: {md: 'none', xs: 'flex'},
                    color: 'text.primary',
                    fontFamily: 'inherit',
                    marginRight: '30px',
                    fontSize: '15px'
                }}
            >
                DISCORD
            </Button>
            <Button
                variant="text"
                sx={{
                    display: {md: 'none', xs: 'flex'},
                    color: 'text.primary',
                    fontFamily: 'inherit',
                    fontSize: '15px'
                }}
            >
                TWITTER
            </Button>
        </Box>
    );
}

export default Footer;