import React from 'react';
import { Container } from '@mui/material';


import Header from '../../components/Header';
import Minting from '../../components/Minting';
import Footer from '../../components/Footer';

const Home = () => {
    return(
        <Container maxWidth='xl'>
            <Header />
            <Minting />
            <Footer />
        </Container>
    );
}

export default Home;