import { useState } from 'react';
import { Box } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';


export default function BasicAccordion() {

    const accordions = [
        {
            title: 'What do you get when you mint a pass ?',
            contents: [
                '+ Access to our first 3 Mobile Games before the general public with competitions and RL prizes.',
                '+ Access to our members only DAO community',
                '+ Free Vote on each DAO proposal',
                '+ Access to our SAFT agreement',
                '+ PLUS MUCH MORE!!',
            ],
        },
        {
            title: 'How magic meta mushrooms plan to be a Top NFT Brand for years to come?',
            contents: [
                '+ May 4th - Deliver MVP of first game to Community, take feedback and submit proposals to community.',
                '+ Deliver 3 Mobile Games - that will become P2E once Foundation has been set up and licenses are acquired.',
                '+ All games are based on the hyper sensitive model used in games such as flappy birds, temple run and more',
                '+ Taking what already works and improving by adding  NFT assets, RL prizes and an overlying story line that is controversial and global competitions to create hype and community',
            ],
        },
        {
            title: 'What is NFTplugDAO roadmap?',
            contents: [
                '+ Core team and asset assembled - r&d',
                '+ Company registration - Q1 2023',
                '+ Token issue Company - Q2 2023',
                '+ Foundation - Q3 2023',
                '+ ICO Liquidity pools - Q4 2023',
            ],
        },
        {
            title: 'Where can we find NFTplugDAO etho?',
            contents: [
                '+ We are striving to innovate, to push the boundaries on what is possible whilst keeping customers happy',
                '+ We are built on Solana and love the network but we believe interoperability is pivotal in creating an open and inclusive metaverse and long term aim is to make all our products easily accessible to all',
                '+ We deliver entertainment and a great experience to our customers by keeping it real.',
                '+ Negativity can and will be expelled from the community and Positivity will be rewarded',
                '+ We build each other up and we chase our dreams, were just here to have FUN, guy!!',
            ],
        },
    ];

    const [expanded, setExpanded] = useState({value: [true,true,true,true]});

    const handleChange = (panel) => (event, isExpanded) => {
        let temp = expanded;
        temp.value[panel] = isExpanded ? true : false;
        setExpanded({...temp});
        console.log(expanded.value);
    };


    return (
        <Box>
            {accordions.map((item, index) => (
                <Accordion disableGutters expanded={expanded.value[index]} key={index} onChange={handleChange(index)} >
                    <AccordionSummary
                        expandIcon={expanded.value[index]? <RemoveIcon sx={{color: '#FFF', fontSize: {md: '24px', xs: '12px'}}}/> : <AddIcon sx={{color: '#FFF', fontSize: {md: '24px', xs: '12px'}}}/>}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        sx={{
                            backgroundColor: '#000',
                        }}
                    >
                        <Typography fontFamily='Audiowide' fontSize={{md: '24px', xs: '12px'}}>{item.title}</Typography>
                    </AccordionSummary>
                    <AccordionDetails
                        sx={{
                            backgroundColor: '#000'
                        }}
                    >
                        {item.contents.map((content, index) => (
                            <Typography key={index} marginY='12px' marginX='10px' color='secondary' fontFamily='Audiowide' fontSize={{md: '20px', xs: '10px'}}>
                                {content}
                            </Typography>
                        ))}
                    </AccordionDetails>
                </Accordion>
            )) }
        </Box>
    );
}