import React from 'react';
import { Box, Button, Typography } from '@mui/material';

import CountDown from '../CountDown';
import ShadowedTitle from '../ShadowedTitle';
import Screen from '../Screen';
import HeroAvatar from '../../assets/images/hero-avatar.svg';
import BasicAccordion from '../Accordion';

const Minting = () => {
    const stages = [
        '1st stage - 1 Sol - March 4th',
        '2nd stage - 1.5 Sol - April 4th',
        'Final Stage - 2 Sol - May 4th',
    ];
    const screens = [
        {
            img: '/assets/images/screens/screen1.svg',
            title: 'GAME I',
            date: '2024'
        },
        {
            img: '/assets/images/screens/screen2.svg',
            title: 'GAME II',
            date: '2034'
        },
        {
            img: '/assets/images/screens/screen3.svg',
            title: 'GAME III',
            date: '2056'
        },
    ];
    const onMintNFT = () => {

    }

    return(
        <Box
            display='flex'
            flexDirection='column'
            alignItems='center'
        >
            <ShadowedTitle title='Whitelist Now Minting' size={{sm: '36px', xs: '18px'}} margin='50px 0px 50px 0px'/>
            <CountDown />
            <Box
                component='img'
                width={{sm: '40%', xs: '80%'}}
                margin='10px'
                src={HeroAvatar}
            />
            <Button
                variant='contained'
                sx={{
                    fontFamily: 'Audiowide',
                    fontSize: {md: '40px', xs: '20px'}
                }}
                onClick={onMintNFT}
            >
                MINT NFT
            </Button>
            <ShadowedTitle title='Minting price - 0.5 sol' size={{sm: '18px', xs: '12px'}} margin='24px 0px 24px 0px' color='secondary'/>
            <Box
                marginY='24px'
            >
                <ShadowedTitle title='NFTplugDAO & Magic Meta Mushrooms' size={{sm: '32px', xs: '12px'}} margin='0px 0px 8px 0px'/>
                <ShadowedTitle title='Early Access Pass' size={{sm: '32px', xs: '12px'}} margin='8px 0px 0px 0px'/>
            </Box>
            <Box>
                { stages.map((stage, index) => (
                    <Typography
                        key={index}
                        fontFamily='Audiowide'
                        marginY='12px'
                        fontSize={{sm: '24px', xs: '14px'}}
                    >
                        {stage}
                    </Typography>
                ))}
            </Box>
            <Box
                display='flex'
                justifyContent='space-between'
                flexDirection={{sm: 'row', xs: 'column'}}
                marginY='10px'
            >
                { screens.map((item, index) => (
                    <Screen
                        key={index} 
                        component='img' 
                        src={item.img}
                        width={{sm: '32%', xs: '100%'}}
                        title={item.title}
                        date={item.date}
                    />
                ))}
            </Box>
            <Box>
                <ShadowedTitle title='Global Battle' size={{sm: '32px', xs: '20px'}} margin='20px 0px 12px 0px'/>
                <Typography
                        fontFamily='Audiowide'
                        marginY='12px'
                        fontSize={{sm: '24px', xs: '14px'}}
                        textAlign='center'
                    >
                        Rewards, R2W, Build up weapons + Skills, Pre season mini games
                </Typography>
            </Box>
            <Box
                marginY={{md: '60px', xs: '30px'}}
            >
                <Typography
                    fontFamily='Audiowide'
                    fontSize={{md: '36px', xs: '18px'}}
                    textAlign='center'
                >
                    WORLD COMPETITIVE KICKOFF
                </Typography>
                <ShadowedTitle title='2024' size={{md: '150px', xs: '70px'}} margin='0px 0px 0px 0px'/>
            </Box>
            <Box>
                <ShadowedTitle title='What do you need to know?' size={{md: '32px', xs: '20px'}} margin='0px 0px 60px 0px'/>
            </Box>
            <BasicAccordion />
        </Box>
    );
} 

export default Minting;