import { useEffect, useState } from "react";
import { Box, Typography } from "@mui/material";

const CountDown = () => {
  // const dispatch = useDispatch();
  const [publicsaledate, setPublicsaledate] = useState(new Date("12 January 2022 16:00:00 UTC").getTime());
  const [timerInterval, setTimerInterval] = useState(null);
  const [publicSale, setPublicSale] = useState(false);
  const [time, setTime] = useState([
    { text: "hours", value: 1 },
    { text: "minutes", value: 25 },
    { text: "seconds", value: 55 },
  ]);


  useEffect(() => {
    updateTime();
    setTimerInterval(setInterval(updateTime, 1000));
    return () => {
      if (timerInterval !== null) {
        clearInterval(timerInterval);
      }
    }
  }, [publicsaledate]) 

  const updateTime = () => {
    let timeleft = publicsaledate - Date.now();
    if (timeleft > 0) {
      setTime([
        { 
          text: "hours", 
          value: Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) 
        },
        { 
          text: "minutes", 
          value: Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60))
        },
        { 
          text: "seconds", 
          value: Math.floor((timeleft % (1000 * 60)) / 1000)
        },
      ]);
    }

    if (timeleft < 0) {
      setPublicSale(true);
      setPublicsaledate(new Date("13 January 2022 16:00:00 UTC").getTime())
    }
  }
  return (
    <Box
        display='flex'
        justifyContent='center'
    >
        { time.map((block, index) => (
            <Box
                display='flex'
                alignItems='end'
                key={index}
            >
                <Box
                    display='flex'
                    flexDirection='column'
                    alignItems='center'
                    marginX={{md: '40px', xs: '10px'}}
                >
                    <Typography
                        fontFamily='Audiowide'
                        fontSize='20px'
                    >
                        {block.text}
                    </Typography>
                    <Typography
                        variant='h4'
                        fontFamily='Audiowide'
                        fontSize={{sm: '60px', xs: '36px'}}
                    >
                        {block.value}
                    </Typography>
                </Box>
                {index !== 2 && <Typography
                    fontFamily='Audiowide'
                    variant='h4'
                    fontSize={{sm: '60px', xs: '36px'}}
                >
                    :
                </Typography>}
            </Box>   
        ))}
    </Box>
  );
}

export default CountDown;