import React from 'react';
import { Typography } from '@mui/material';

const ShadowedTitle = (props) => {
    const { title, size, margin, color } = props;
    return (
        <Typography
            textAlign='center'
            fontSize={size}
            fontFamily='Audiowide'
            fontWeight='bold'
            letterSpacing='0.3em'
            margin={margin}
            color={color}
            sx={{
                textShadow: '0px 0px 25px rgba(234, 18, 148, 1), 0px 0px 25px #EA1294',
            }}
        >
            {title}
        </Typography>
    );
}

export default ShadowedTitle;