import React from 'react';
import { Box, Typography } from '@mui/material';

const Screen = (props) => {
    const { key, src, width, title, date } = props;
    return(
        <Box
            position='relative'
            width={width}
            marginY={{md: '30px', xs: '10px'}}
        >
            <Box 
                key={key} 
                component='img' 
                src={src}
                width='100%'
            />
            <Box
                display='flex'
                justifyContent='space-between'
                width='80%'
                position='absolute'
                bottom='10px'
                left='50%'
                sx={{
                    transform: 'translate(-50%, 0px)',
                }}
            >
                <Typography
                    fontFamily='Audiowide'
                    fontSize={{md: '24px', xs: '16px'}}
                >
                    {title}
                </Typography>
                <Typography
                    fontFamily='Audiowide'
                    fontSize={{md: '24px', xs: '16px'}}
                >
                    {date}
                </Typography>
            </Box>
        </Box>
    );
}

export default Screen;